import { Col, Container, Row } from 'react-bootstrap';
import styled from 'styled-components';
import naverBlog from '../imgs/archiving/naverBlog.png';
import git from '../imgs/archiving/git.png';
import ArchivingCard from './elements/ArchivingCard';

const MyContainer = styled(Container)`
  background: linear-gradient(90deg, #131417, #2f313a 35% 65%, #131417);
`;

function component() {
  return (
    <MyContainer fluid={true}>
      <Container className="pb-5">
        <Row className="text-center py-5">
          <h1>
            <b>Archiving</b>
          </h1>
        </Row>
        <Row className="d-flex flex-row justify-content-center">
          <Col lg={6}>
            <ArchivingCard>
              <li className="member">
                <div className="thumb">
                  <img src={naverBlog} />
                </div>
                <div className="description">
                  <h3>Blog</h3>
                  <div>
                    <h5 className="text-dark m-5">   .</h5>
                    <ul>
                      <li className="m-1">
                                  
                      </li>
                      <li className="m-1">        </li>
                      <li className="m-1">   </li>
                      <li className="m-1">        </li>
                    </ul>
                    <br />
                    <a href="https://blog.naver.com/minqt98" target="_blank">
                      @NaverBlog
                    </a>
                  </div>
                </div>
              </li>

              <li className="member">
                <div className="thumb">
                  <img src={git} />
                </div>
                <div className="description">
                  <h3>Code</h3>
                  <div>
                    <h5 className="text-dark m-5"> .</h5>
                    <ul>
                      <li className="m-1"> , ,   </li>
                      <li className="m-1">      </li>
                      <li className="m-1">       </li>
                    </ul>
                    <br />
                    <a href="https://github.com/mincheolhong" target="_blank">
                      @GitHub
                    </a>
                  </div>
                </div>
              </li>
            </ArchivingCard>
          </Col>
        </Row>
      </Container>
    </MyContainer>
  );
}

export default component;
